// Algoritmos de implementao das codificaes em banda base

/*

Configurao do grfico:

Nvel                          Valor no grfico         
Nvel Alto (+)           =>     1
Ausncia de Sinal (0)    =>     0
Nvel Baixo (-)          =>    -1

*/

// Codificao NRZI
// Caracterstica: No ocorre transio entre nvel alto e baixo com bit 0, apenas com bit 1

export function converteNrzi(sequencia) { // Sequncia de entrada do input como parmetro

  const nrziSeq = []; // Array para receber a sequncia codificada em NRZI
  var estado = "0"; // Inicializao da varivel que guarda o estado para comparao
  var i = 0; // Inicializao do contador do lao

  while (i < sequencia.length) { // Lao que percorre toda a sequncia de entrada

    if (i === 0) { // Se for o primeiro bit da sequncia
      
      if (sequencia[i] === "0") { // Se o primeiro bit for 0, inicia no nvel baixo
        nrziSeq.push(-1); // Nvel Baixo (-) => -1
        estado = "-1"; // Guarda o estado para comparao
        i++;
      }

      if (sequencia[i] === "1") { // Se o primeiro bit for 1, inicia no nvel alto
        nrziSeq.push(1); // Nvel Alto (+) => 1
        estado = "1"; // Guarda o estado para comparao
        i++;
      }

    } else if (i > 0) { // Para os bits seguintes

      if (sequencia[i] === "0") { // Se o bit for 0, no ocorre transio

        if (estado === "1") { // Se estava no nvel alto, mantm
          nrziSeq.push(1); // Nvel Alto (+) => 1
          i++;
        } else if (estado === "-1") { // Se estava no nvel baixo, mantm
          nrziSeq.push(-1); // Nvel Baixo (-) => -1
          i++;
        }

      } else if (sequencia[i] === "1") { // Se o bit for 1, ocorre transio

        if (estado === "1") { // Se estava no nvel alto, vai para o nvel baixo
          nrziSeq.push(-1); // Nvel Baixo (-) => -1
          estado = "-1"; // Guarda o estado atual para comparao
          i++;
        } else if (estado === "-1") { // Se estava no nvel baixo, vai para o nvel alto
          nrziSeq.push(1); // Nvel Alto (+) => 1
          estado = "1"; // Guarda o estado atual para comparao
          i++;
        }

      }
    }
  }

  return nrziSeq; // Retorna a sequncia codificada em NRZI para o grfico
}

// Codificao Pseudoternary
/*
Caracterstica: Bit 1 representa ausncia de sinal e bit 0 representa 
a alterncia entre pulsos positivos e negativos
*/

export function convertePseudo(sequencia) { // Sequncia de entrada do input como parmetro
  
  const pseudoSeq = []; // Array para receber a sequncia codificada em Pseudoternary
  var estado = "-1"; // Inicializao da varivel que guarda o estado para comparao
  var i = 0; // Inicializao do contador do lao

  while (i < sequencia.length) { // Lao que percorre toda a sequncia de entrada

    if (i === 0) { // Se for o primeiro bit da sequncia

      if (sequencia[i] === "0") { // Se o primeiro bit for 0, inicia no nvel alto
        pseudoSeq.push(1); // Nvel Alto (+) => 1
        estado = "1"; // O pulso positivo  guardado
        i++;
      }

      if (sequencia[i] === "1") { // Se o primeiro bit for 0, h ausncia de sinal
        pseudoSeq.push(0); // Ausncia de Sinal (0) => 0
        i++;
      }

    } else if (i > 0) { // Para os bits seguintes

      if (sequencia[i] === "0") { // Se o bit for 0, ocorre a alternncia de pulsos

        if (estado === "1") { // Se estava no nvel alto, vai para o nvel baixo
          pseudoSeq.push(-1); // Nvel Baixo (-) => -1
          estado = "-1"; // O pulso negativo  guardado
          i++;
        } else if (estado === "-1") { // Se estava no nvel baixo, vai para o nvel alto
          pseudoSeq.push(1); // Nvel Alto (+) => 1
          estado = "1"; // O pulso positivo  guardado
          i++;
        }

      } else if (sequencia[i] === "1") { // Se o bit for 1, h ausncia de sinal
        pseudoSeq.push(0); // Ausncia de Sinal (0) => 0
        i++;
      }

    }
  }

  return pseudoSeq; // Retorna a sequncia codificada em Pseudoternary para o grfico
}


// Codificao 4B3T
/* Caracterstica: Converso de agrupamentos de 4 bits para 3 bits, leva em
considerao o acmulo de disparidade DC */

export function converte4B3T(sequencia) {
  var disparidade = -2; // A disparidade inicial adotada
  const soma = (x, y) => { // Funo de soma para pular o zero
    if ((x + y) === 0 ) {
      if (y > 0) {
        return 1;
      } else if (y < 0) {
        return -1;
      }
    }
  }

  // Funo para a converso dos agrupamentos de 4 bits em 3 bits
  const codificaTernario = (grupo) => { // O grupo de 4 bits  passado como parmetro
    
    switch (grupo) { // Casos para cada possibilidade de agrupamento
      
      case "0000":
        return [0, -1, 1]; // 0 - +, (+0)

      case "0001":
        return [-1, 1, 0]; // - + 0, (+0)

      case "0010":
        return [-1, 0, 1]; // + - 0, (+0)

      case "0011":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,1); // (+1)
          return [1, -1, 1]; // + - +
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-1); // (-1)
          return [-1, 1, -1]; // - + -
        }

      case "0100":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,2); // (+2)
          return [0, 1, 1]; // 0 + +
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-2); // (-2)
          return [0, -1, -1]; // 0 - -
        }

      case "0101":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,1); // (+1)
          return [0, 1, 0]; // 0 + 0
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-1); // (-1)
          return [0, -1, 0]; // 0 - 0
        }

      case "0110":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,1); // (+1)
          return [0, 0, 1]; // 0 0 +
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-1); // (-1)
          return [0, 0, -1]; // 0 0 -
        }

      case "0111":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,1); // (+1)
          return [-1, 1, 1]; // - + +
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-1); // (-1)
          return [1, -1, -1]; // + - -
        }

      case "1000":
        return [0, 1, -1]; // 0 + -, (+0)

      case "1001":
        return [1, -1, 0]; // + - 0, (+0)

      case "1010":
        return [1, 0, -1]; // + 0 -, (+0)

      case "1011":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,1); // (+1)
          return [1, 0, 0]; // + 0 0
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-1); // (-1)
          return [-1, 0, 0]; // - 0 0
        }

      case "1100":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,2); // (+2)
          return [1, 0, 1]; // + 0 +
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-2); // (-2)
          return [-1, 0, -1]; // - 0 -
        }

      case "1101":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,2); // (+2)
          return [1, 1, 0]; // + + 0
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-2); // (-2)
          return [-1, -1, 0]; // - - 0
        }

      case "1110":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,1); // (+1)
          return [1, 1, -1]; // + + -
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-1); // (-1)
          return [-1, -1, 1]; // - - +
        }

      case "1111":
        if (disparidade < 0) { // Modo positivo
          disparidade = soma(disparidade,3); // (+3)
          return [1, 1, 1]; // + + +
        } else if (disparidade > 0) { // Modo negativo
          disparidade = soma(disparidade,-3); // (-3)
          return [-1, -1, -1]; // - - -
        }

      default:
    }
  };

  var qbttSeq = []; // Array para receber a sequncia codificada em 4B3T

  // Diviso da sequncia de entrada em agrupamentos de 4 bits
  const agrupamentos = sequencia.split(/(.{4})/).filter((O) => O);

  // Lao para percorrer o array de agrupamentos
  for (var i = 0; i < agrupamentos.length; i++) {
    // Cada grupo de 4  codificado em uma sequncia de 3 pulsos, adicionada ao array
    qbttSeq.push.apply(qbttSeq, codificaTernario(agrupamentos[i]));
  }

  return qbttSeq; // Retorna a sequncia codificada em 4B3T para o grfico
}
